import React, { Component, Fragment } from 'react';
import { Context } from '../context';
import { MainContent, PostCard } from '../layouts';
import { injectPageMetaData } from '../utils';
import { blog } from '../data/metadata';
import usernames from '../data/usernames';

class Blog extends Component {
  componentDidMount() {
    injectPageMetaData(blog);
  }

  render() {
    const { blogPosts } = this.context;
    return (
      <MainContent>
        <div className="Blog">
          <h1>Blog</h1>
          <p>
            I love to share what I know. So, in my free time, I write articles
            on JavaScript and related stuff. Visit my{' '}
            <a
              href={`https://${usernames.medium}.medium.com`}
              target="_blank"
              rel="noopener noreferrer"
            >
              Medium profile
            </a>{' '}
            for more.
          </p>
          {blogPosts.map((post, i) => (
            <Fragment key={post.title}>
              <PostCard post={post} />
              {i < blogPosts.length - 1 && <hr />}
            </Fragment>
          ))}
        </div>
      </MainContent>
    );
  }
}

Blog.contextType = Context;

export default Blog;
