import React, { Component } from 'react';
import { MainContent } from '../layouts';
import ContactForm from '../misc/ContactForm';
import { injectPageMetaData } from '../utils';
import { contact } from '../data/metadata';
import usernames from '../data/usernames';

class Contact extends Component {
  componentDidMount() {
    injectPageMetaData(contact);
  }

  render() {
    const { email } = usernames;

    return (
      <MainContent>
        <div className="Contact position-relative">
          <h1>Contact Me</h1>
          <p>
            Have an idea to be implemented or want to work together? Please
            reach out to{' '}
            <a href={`mailto:${email}`} title={`Mail: ${email}`}>
              {email}
            </a>{' '}
            or leave me a message below and I will get back to you soon.
          </p>
          <ContactForm />
        </div>
      </MainContent>
    );
  }
}

export default Contact;
