import React, { Component } from 'react';
import { Context } from '../context';
import { faChevronLeft } from '@fortawesome/free-solid-svg-icons';
import NotFound from './NotFound';
import { MainContent, Project, ButtonLink } from '../layouts';
import { injectPageMetaData } from '../utils';
import { projects } from '../data/metadata';

class ProjectDetails extends Component {
  constructor(props) {
    super(props);
    this.projectId = this.props.match.params.projectId;
  }

  componentDidMount() {
    const project = projects.find((project) => project.id === this.projectId);
    if (project) injectPageMetaData(project);
  }

  render() {
    const project = this.context.getProject(this.projectId);
    if (!project) {
      return <NotFound />;
    }
    return (
      <MainContent>
        <div className="ProjectDetails">
          <ButtonLink
            icon={faChevronLeft}
            onClick={() => this.props.history.push('/portfolio')}
          >
            Back to Portfolio
          </ButtonLink>
          <Project project={project} detailed />
        </div>
      </MainContent>
    );
  }
}

ProjectDetails.contextType = Context;

export default ProjectDetails;
