import React, { Component, Fragment } from 'react';
import { Context } from '../context';
import { MainContent, Project } from '../layouts';
import { injectPageMetaData } from '../utils';
import { portfolio } from '../data/metadata';

class Portfolio extends Component {
  componentDidMount() {
    injectPageMetaData(portfolio);
  }

  render() {
    const { projects } = this.context;
    return (
      <MainContent>
        <div className="Portfolio">
          <h1>Portfolio</h1>
          <p>
            I have developed, launched, and maintained multiple projects from
            scratch, some of them are posted here. Each project includes all the
            details about technologies and methods used during the project life
            cycle.
          </p>
          {projects.map((project, i) => (
            <Fragment key={project.id}>
              <Project project={project} />
              {i < projects.length - 1 && <hr />}
            </Fragment>
          ))}
        </div>
      </MainContent>
    );
  }
}

Portfolio.contextType = Context;

export default Portfolio;
